import { ReactNode, useState, useRef, useEffect } from 'react'
import classnames from 'classnames'
import { KeyValue } from '../../lib/keyboard'
import { CharStatus } from '../../lib/statuses'

type Props = {
  children?: ReactNode
  value: KeyValue
  width?: number
  status?: CharStatus
  onClick: (value: KeyValue) => void
  isEnter?: boolean
  isDelete?: boolean
  longPressOptions?: string[]  // Available options for long press
}

export const Key = ({
  children,
  status,
  width = 40,
  value,
  onClick,
  isEnter = false,
  isDelete = false,
  longPressOptions = [],
}: Props) => {
  const [showPopup, setShowPopup] = useState(false)
  const longPressTimerRef = useRef<NodeJS.Timeout | null>(null)
  const hasLongPress = longPressOptions.length > 0

  const classes = classnames(
    'flex items-center justify-center rounded mx-0.5 text-lg font-bold cursor-pointer select-none relative',
    {
      'bg-slate-200 hover:bg-slate-300 active:bg-slate-400': !status && !isEnter && !isDelete,
      'bg-slate-400 text-white': status === 'absent',
      'bg-red-500 hover:bg-red-600 active:bg-red-700 text-white':
        status === 'correct',
      'bg-yellow-400 hover:bg-yellow-600 active:bg-yellow-700 text-white':
        status === 'present',
      'bg-green-200 hover:bg-green-300 active:bg-green-400 text-gray-800': isEnter,
      'bg-red-200 hover:bg-red-300 active:bg-red-400 text-gray-800': isDelete,
    }
  )

  // Simple long press detection
  const startLongPress = () => {
    if (hasLongPress) {
      longPressTimerRef.current = setTimeout(() => {
        setShowPopup(true)
      }, 400) // 400ms long press
    }
  }

  const cancelLongPress = () => {
    if (longPressTimerRef.current) {
      clearTimeout(longPressTimerRef.current)
      longPressTimerRef.current = null
    }
  }

  // Handle clicking popup options
  const handlePopupClick = (option: string, event: React.MouseEvent) => {
    event.preventDefault()
    event.stopPropagation()
    setShowPopup(false)
    onClick(option as KeyValue)
  }

  // Handle normal key clicks
  const handleClick = () => {
    if (!showPopup) {
      onClick(value)
    }
  }

  // Handle touch start
  const handleTouchStart = (event: React.TouchEvent) => {
    if (hasLongPress) {
      event.preventDefault()
      startLongPress()
    }
  }

  // Handle touch end
  const handleTouchEnd = (event: React.TouchEvent) => {
    if (hasLongPress) {
      event.preventDefault()
      cancelLongPress()
      if (!showPopup) {
        onClick(value) // Quick tap
      }
    }
  }

  // Handle mouse events
  const handleMouseDown = () => {
    if (hasLongPress) {
      startLongPress()
    }
  }

  const handleMouseUp = () => {
    if (hasLongPress) {
      cancelLongPress()
      if (!showPopup) {
        onClick(value) // Quick click
      }
    }
  }

  const handleMouseLeave = () => {
    if (hasLongPress) {
      cancelLongPress()
      setShowPopup(false)
    }
  }

  // Cleanup timer on unmount
  useEffect(() => {
    return () => {
      if (longPressTimerRef.current) {
        clearTimeout(longPressTimerRef.current)
      }
    }
  }, [])

  return (
    <div className="relative">
      <button
        style={{ width: `${width}px`, height: '58px' }}
        className={classes}
        onClick={hasLongPress ? undefined : handleClick}
        onMouseDown={hasLongPress ? handleMouseDown : undefined}
        onMouseUp={hasLongPress ? handleMouseUp : undefined}
        onMouseLeave={hasLongPress ? handleMouseLeave : undefined}
        onTouchStart={hasLongPress ? handleTouchStart : undefined}
        onTouchEnd={hasLongPress ? handleTouchEnd : undefined}
      >
        {children || value}
      </button>
      
      {/* Simple popup with macron options */}
      {showPopup && longPressOptions.length > 0 && (
        <div className="absolute bottom-full left-1/2 transform -translate-x-1/2 mb-2 bg-gray-800 text-white rounded-lg px-2 py-1 shadow-lg z-50">
          <div className="flex space-x-1">
            {longPressOptions.map((option) => (
              <button
                key={option}
                className="px-2 py-1 text-lg font-bold hover:bg-gray-700 rounded"
                onClick={(event) => handlePopupClick(option, event)}
              >
                {option}
              </button>
            ))}
          </div>
          {/* Arrow pointing down */}
          <div className="absolute top-full left-1/2 transform -translate-x-1/2 w-0 h-0 border-l-4 border-r-4 border-t-4 border-transparent border-t-gray-800"></div>
        </div>
      )}
    </div>
  )
}
